import React from 'react'
import './style.css'

const ResponsiveButtons = (props) =>
    (
        <div className="text-center">
            <button id="tablet" onClick={ ()=> props.getDevice("tablet") }><i className="fas fa-tablet-alt"/></button>
            <button id="desktop" onClick={ ()=> props.getDevice("desktop") }><i className="fas fa-desktop"/></button>
            <button id="phone" onClick={ ()=> props.getDevice("phone")}><i className="fas fa-mobile-alt"/></button>
        </div>
    );

export default ResponsiveButtons;


