export function getValues(array, search) {
    return array
        .filter(function (values) {
            return (values.id === search)
        })
        .map(function (item) {
            return item;
        })[0];
}

export function getUrl(id, params) {
    let url;
    if (id === "tablet") {
        url = params.tabletImg
    }
    else if (id === "desktop") {
        url = params.deskImg
    }
    else {
        url = params.phoneImg
    }
    return url;
}


