import React, {Component} from 'react';
import './style.css';
import article from '../../fixtures';
import {getValues} from '../../tools';
import LinksBtns from './LinksBtns'
import Demos from './Demos'
import Header from "./Header";
import DemosMobile from "./DemosMobile";

export default class Detail extends Component {
    render() {
        const {match} = this.props;
        const getParams = getValues(article, match.params.id);
        return (
            <div className="col-xs-12">
                <div className="row">
                    <Header getParams={getParams}/>
                </div>

                <div className="row">
                    <div className="container">
                        <div className="row text-center hidden-xs">
                            <Demos getParams={getParams}/>
                        </div>
                    </div>
                </div>
                <div className="text-center visible-xs">
                    <DemosMobile getParams={getParams}/>
                </div>

                <LinksBtns getParams={getParams}/>
            </div>
        );
    }
}


