import React, {Component} from 'react';
import './style.css';
import ResponsiveButtons from '../ResponsiveButtons/ResponsiveButtons'
import {getUrl} from "../../tools";

export default class DemosMobile extends Component {

    state = {
        visible: true,
        imgUrl: this.props.getParams.deskImg,
    };

    getDevice = (id) => {
        console.log(id);
        const i = getUrl(id, this.props.getParams);
        console.log(i);
        this.setState({imgUrl: i})
       // console.log(this.props.getParams)
    };
    render() {
        console.log(this.props.getParams.deskImg);
        const {getParams} = this.props;
        if (!this.state.visible) {
            return null;
        }
        return (

            <div className="devices">
                <div className="row responsives">
                    <ResponsiveButtons getDevice={this.getDevice} />
                </div>
                <div className="imgDevices">
                    <img src={this.state.imgUrl} alt="tablet"/>
                </div>
            </div>

        );
    }
}



