import React, {Component} from 'react';
import './style.css';

export default class Demos extends Component {

    state = {
        isOpen: true
    }

    render() {
        const {getParams} = this.props;
        return (

            <div className="devices">
                <div className="imgDevices imgTablet">
                    <img src={getParams.tabletImg} alt="tablet"/>
                </div>
                <div className="imgDevices imgDesktop">
                    <img src={getParams.deskImg} alt="desktop"/>
                </div>
                <div className="imgDevices imgMobile">
                    <img src={getParams.phoneImg} alt="mobile"/>
                </div>
            </div>

        );
    }
}


