import React, {Component} from 'react';
import './style.css';
import {Link} from "react-router-dom";

export default class LinksBtns extends Component {

    render() {
        const {getParams} = this.props;
        {console.log(getParams.link)}
        return (
            <div className="btns-links">
                <Link to="/">
                    <button><i className="fas fa-share"></i>  </button>
                </Link>
                <form action={getParams.link} target="_blank">
                    <button>  </button>
                </form>
            </div>
        );
    }
}


