import React, {Component} from 'react';
import './style.css';

export default class Detail extends Component {


    render() {
        const {getParams} = this.props;
        return (
            <div className="col-xs-12">
                <h1>{getParams.title}</h1>
                <p>{getParams.description}</p>
            </div>
        );
    }
}


